<template>
    <v-navigation-drawer
            v-model="drawer"
            :mini-variant="miniVariant"
            :permanent="permanent"
            :src="bg"
            absolute
            width="282"
            mini-variant-width="103"
            class="side"
    >
        <v-list
                dense
                nav
                class="py-0"
                :class="!miniVariant && 'v-list--custom'"
        >
            <v-list-item two-line v-if="miniVariant" class="mb-11 mt-5">
                <v-row
                        align="center"
                        justify="center"
                >
                    <v-badge
                            bordered
                            color="error"
                            content="3"
                            overlap
                    >
                        <avatar :username="login" :size="65" color="#fff"></avatar>
                    </v-badge>
                </v-row>
            </v-list-item>

            <v-list-item two-line v-if="!miniVariant">
                <v-list-item-avatar size="65">
                    <avatar :username="login" :size="65" color="#fff"></avatar>
                </v-list-item-avatar>

                <v-list-item-content class="side__notification">
                    <v-row
                            align="center"
                            justify="center"
                    >
                        <v-badge
                                bordered
                                color="error"
                                content="3"
                                overlap
                        >
                            <img width="42" :src="require('@/assets/img//sidebar/bell.svg')" alt="">
                        </v-badge>
                    </v-row>
                </v-list-item-content>
            </v-list-item>
            <v-list-item>
                <p class="side__login">{{login}}</p>
            </v-list-item>

            <v-divider></v-divider>

            <v-list-item class="side__balance">
                <v-list-item-content v-if="!miniVariant">
                    <v-list-item-title>На счету</v-list-item-title>
                </v-list-item-content>
                <v-list-item-icon :class="miniVariant && 'ml-2'">
                    <img width="20" :src="require('@/assets/img//sidebar/coin.svg')" alt="">
                </v-list-item-icon>
                <v-list-item-content :class="miniVariant && 'coins-mobile'">
                    <v-list-item-title>{{coins}}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>

            <v-list-item link :class="miniVariant && 'px-0'">
                <v-list-item-icon>
                    <img width="20" :src="require('@/assets/img//sidebar/card.svg')" alt="">
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>Пополнить</v-list-item-title>
                </v-list-item-content>
            </v-list-item>

            <v-divider></v-divider>

            <v-list-item
                    v-for="item in items"
                    :key="item.title"
                    link
                    :class="miniVariant && 'px-0'"
            >
                <v-list-item-icon>
                    <img width="20" :src="require(`@/assets/img//sidebar/${item.icon}`)" alt="">
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>{{ item.title }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            <v-divider></v-divider>
            <v-list-item link :class="miniVariant && 'px-0'">
                <v-list-item-icon>
                    <img width="20" :src="require('@/assets/img/sidebar/settings.svg')" alt="">
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>Настройки</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
            <v-list-item link :class="miniVariant && 'px-0'">
                <v-list-item-icon>
                    <img width="20" :src="require('@/assets/img/sidebar/logout.svg')" alt="">
                </v-list-item-icon>

                <v-list-item-content>
                    <v-list-item-title>Выйти</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
        </v-list>
        <div class="side__arrow" :class="{'hidden' : miniVariant}" @click="expand">
            <img width="32" :src="require('@/assets/img//sidebar/expand.svg')" alt="">
        </div>
    </v-navigation-drawer>
</template>

<script>
    import Avatar from 'vue-avatar'

export default {
    name: "Sidebar",
    components: {
        Avatar
    },
    data () {
        return {
            login: 'VPutin',
            coins: '50',
            drawer: true,
            items: [
                { title: 'Профиль', icon: 'profile.svg' },
                { title: 'Мои задания', icon: 'task.svg' },
            ],
            permanent: true,
            miniVariant: false,
            background: false,
        }
    },
    methods: {
        expand () {
            this.miniVariant = !this.miniVariant
        }
    },
    computed: {
        bg () {
            return this.background ? 'https://cdn.vuetifyjs.com/images/backgrounds/bg-2.jpg' : undefined
        },
    },
}
</script>

<style lang="sass" scoped>
.v-navigation-drawer
    position: relative
    background: #F7F7F8
    box-shadow: 4px 4px 10px #EBEBEE
    border-radius: 15px
    overflow: initial
    padding: 60px 0
.v-divider
    margin: 25px 0
    border: 2px solid #E8E8EF
.side
    &__balance
        flex: 0
        justify-content: flex-start
        .v-list-item__icon
            margin-right: 0 !important
            margin-left: -15px
    &__arrow
        position: absolute
        top: 50vh
        right: -15px
        cursor: pointer
        transition: 0.1s
        &.hidden
            transform: rotate(180deg)
    &__login
        font-style: normal
        font-weight: bold
        font-size: 18px
        line-height: 110%
        color: #353030
        margin-bottom: 0 !important
    .v-list--custom
        padding-right: 24px
        padding-left: 24px
    &__notification
        cursor: pointer
    .v-list-item
        &__title
            font-weight: 500
            font-size: 16px
            line-height: 110%
            color: #353030
        &__icon
            margin-right: 11px
    .coins-mobile
        height: auto
        position: static !important
</style>

<style lang="sass">
    .v-navigation-drawer
        &__border
            display: none
</style>